
import { defineComponent } from "@vue/runtime-core";
import { store } from "../store";
export default defineComponent({
  name: "WatchList",
  data() {
    return {
      val: store.watchList,
      cnt:0
    };
  },
  methods: {
    
    removeFromList(v: string) {
      const idx = this.val.indexOf(v);
      this.val.splice(idx, 1);
    },
  },
  unmounted() {
    store.watchList = this.val;
  },
});
